.custom-multi-select {
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 4px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  background-color: #f9f9f9;
}
.selected-items {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.selected-item {
  background-color: #e0e0e0;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: underline;
}
.selected-item:hover {
  background-color: #d6d6d6;
}
